.cards {
   top: -80px;
}


.cards__container {
  align-items: left;
  width: 100%;
  height: 100%;

}

.top-left {
  position: absolute;
  top: 8px;
  left: 16px;
  color: white;
}

.cards__wrapper {
  position: relative;

  @media screen and (max-width: 950px) {
    margin: 0px;
    background-color: red;

  }
 
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  overflow: hidden;
  padding: 100px;
  height: 100px;
  justify-content: flex-end;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;

  padding-top: 100%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 0px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 100;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
  border: none;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
  border: none;
  transform: scale(1.1);

}

.cards__item__img:hover {
  transform: scale(1.2);
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__text {
  position: absolute;
  top: 8px;
  left: 16px;
  color: white;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 950px) {
  .cards__items {
    padding: 0;
  }
}

@media only screen and (max-width: 950px) {
  .cards__item__link {
    height: 300px;
  }
}




